export default class Structure
{
    constructor()
    {
        this.activeCells = [];
    }

    apply(gameState, xOffset, yOffset)
    {
        gameState.setEmptyState();
        for (var i = 0; i < this.activeCells.length; i++)
        {
            gameState.cells[this.activeCells[i][0] + xOffset][this.activeCells[i][1] + yOffset].setDirect(true);
        }
    }
}