import CellState from "./CellState.js";
import CloverLeaf from "./structures/CloverLeaf.js";
import Glider from "./structures/Glider.js";
import Pulsar from "./structures/Pulsar.js";
import Spaceship from "./structures/Spaceship.js";
import FigureEight from "./structures/FigureEight.js";
import DinnerTable from "./structures/DinnerTable.js";
import Tumbler from "./structures/Tumbler.js";
import Pentadecathlon from "./structures/Pentadecathlon.js";


export default class GameState
{
    constructor(xs, ys)
    {
        this.xs = xs;
        this.ys = ys;

        this.beatIndex = 0;

        // create cells
        this.cells = [];
        for (var x = 0; x < xs; x++)
        {
            this.cells.push([]);
            for (var y = 0; y < ys; y++)
            {
                this.cells[x].push(new CellState());
            }
        }

        this.setRandomState();
    }

    tick()
    {
        for (var x = 0; x < this.xs; x++)
        {
            for (var y = 0; y < this.ys; y++)
            {
                if (this.cells[x][y].sound != null)
                {
                    this.cells[x][y].sound.wasTriggeredThisTick = false;
                }
                var numOn = 0;
                let neighbours = this.getAllNeighbours(x, y);
                for (var i = 0; i < neighbours.length; i++)
                {
                    if (neighbours[i].on) numOn++;
                }
                /*
                    https://en.wikipedia.org/wiki/Conway%27s_Game_of_Life
                    1. Any live cell with fewer than two live neighbours dies, as if by underpopulation.
                    2. Any live cell with two or three live neighbours lives on to the next generation.
                    3. Any live cell with more than three live neighbours dies, as if by overpopulation.
                    4. Any dead cell with exactly three live neighbours becomes a live cell, as if by reproduction.
                */
                if (this.cells[x][y].on)
                {
                    if (numOn < 2) this.cells[x][y].prepareNewState(false);
                    else if (numOn > 3) this.cells[x][y].prepareNewState(false);
                }
                else if (numOn == 3)
                {
                    this.cells[x][y].prepareNewState(true);
                }
            }
        }

        // apply
        for (x = 0; x < this.xs; x++)
        {
            for (y = 0; y < this.ys; y++)
            {
                this.cells[x][y].applyNewState();
            }
        }

        this.beatIndex++;
    }

    setEmptyState()
    {
        for (var x = 0; x < this.xs; x++)
        {
            for (var y = 0; y < this.ys; y++)
            {
                this.cells[x][y].setDirect(false);
            }
        }
    }
    setRandomState()
    {
        for (var x = 0; x < this.xs; x++)
        {
            for (var y = 0; y < this.ys; y++)
            {
                this.cells[x][y].setDirect(Math.random() < 0.4);
            }
        }
    }
    setGliderState()
    {
        new Glider().apply(this, 4, 4);
    }
    setSpaceshipState()
    {
        new Spaceship().apply(this, 2, 2);
    }
    setPulsarState()
    {
        new Pulsar().apply(this, 2, 2);
    }
    setCloverLeafState()
    {
        new CloverLeaf().apply(this, 4, 3);
    }
    setFigureEightState()
    {
        new FigureEight().apply(this, 5, 6);
    }
    setDinnerTableState()
    {
        new DinnerTable().apply(this, 2, 2);
    }
    setTumblerState()
    {
        new Tumbler().apply(this, 4, 6);
    }
    setPentadecathlonState()
    {
        new Pentadecathlon().apply(this, 5, 7);
    }

    clearAllSounds()
    {
        for (var x = 0; x < this.xs; x++)
        {
            for (var y = 0; y < this.ys; y++)
            {
                this.cells[x][y].sound = null;
            }
        }
    }


    getAllNeighbours(x, y)
    {
        return [
            this.cells[this.mod(x - 1, this.xs) ][this.mod(y - 1, this.ys)  ],
            this.cells[         x               ][this.mod(y - 1, this.ys)  ],
            this.cells[this.mod(x + 1, this.xs) ][this.mod(y - 1, this.ys)  ],
            this.cells[this.mod(x + 1, this.xs) ][         y                ],
            this.cells[this.mod(x + 1, this.xs) ][this.mod(y + 1, this.ys)  ],
            this.cells[         x               ][this.mod(y + 1, this.ys)  ],
            this.cells[this.mod(x - 1, this.xs) ][this.mod(y + 1, this.ys)  ],
            this.cells[this.mod(x - 1, this.xs) ][         y                ],
        ];
    }

    mod(x, n)
    {
        return (x % n + n) % n;
    }
}