<template>
  <div class="grid-container" @mousedown="onMouseDown" @mouseup="onMouseUp">
    <div v-for="row in gameState.cells" :key="row" class="grid-item">
      <div v-for="item in row" :key="item" class="grid-item">
        <button 
          :class="{
            cell: true, 
            active: item.on,
            highlight: item.justPlayedSound
          }"
          @click="onCellClicked(item)"
          @mousedown="onCellMouseDown(item)"
          @mouseenter="onCellMouseEntered(item)"
          :style="{ 'border-color': item.getSoundColor() }"
        />
      </div>
    </div>
  </div>
  <div class="settings">
    <button 
      v-for="(sound, i) in sounds"
      :key="sound.name"
      :class="{settingsButton: true, activeSetting: selectedSoundIndex==i}"
      :style="{ 'background': sound.color }"
      @click="onSoundSelected(i)">
        {{ sound.name }}
      </button>
      <button class="settingsButton" @click="gameState.clearAllSounds">
        Clear all
      </button>
  </div>
  <div class="setState">
    <button class="settingsButton stateButton" @click="gameState.setEmptyState">
      empty
    </button>
    <button class="settingsButton stateButton" @click="gameState.setRandomState">
      random
    </button>
    <button class="settingsButton stateButton" @click="gameState.setGliderState">
      glider
    </button>
    <button class="settingsButton stateButton" @click="gameState.setSpaceshipState">
      spaceship
    </button>
    <button class="settingsButton stateButton" @click="gameState.setCloverLeafState">
      cloverleaf
    </button>
    <button class="settingsButton stateButton" @click="gameState.setPulsarState">
      pulsar
    </button>
    <button class="settingsButton stateButton" @click="gameState.setFigureEightState">
      figure eight
    </button>
    <button class="settingsButton stateButton" @click="gameState.setDinnerTableState">
      dinner table
    </button>
    <button class="settingsButton stateButton" @click="gameState.setTumblerState">
      tumbler
    </button>
    <button class="settingsButton stateButton" @click="gameState.setPentadecathlonState">
      Pentadecathlon
    </button>
  </div>
  <button class="soundOnOff" @click="onAudioButtonToggle">
    <!-- <img src="./audioButton/off.svg" alt="sound on/off"> -->
    <img :src="this.audioOn ? require('./audioButton/on.svg') : require('./audioButton/off.svg')" alt="sound on/off" width="40">
  </button>
</template>




<script>
import GameState from "./classes/GameState.js"
import Sound from "./classes/Sound.js"

export default {
  name: 'Conway\'s Game of Sound',

  data()
  {
    return {
      gameState: new GameState(17, 17),
      sounds: [],
      selectedSoundIndex: 0,
      mouseDown: false,
      mouseDownTurningOff: false,
      audioOn: false,
    }
  },

  mounted() {
    this.$nextTick(function () {
        window.setInterval(() => {
          this.gameState.tick();
        }, 450);
    });

    this.sounds = [];
    let offset = -15;
    this.sounds.push(new Sound("01", "#0eaa59", 60 + offset));
    this.sounds.push(new Sound("02", "#0eaa8b", 62 + offset));
    this.sounds.push(new Sound("03", "#0e95aa", 64 + offset));
    this.sounds.push(new Sound("04", "#0e6baa", 67 + offset));
    this.sounds.push(new Sound("05", "#0e25aa", 69 + offset));
    this.sounds.push(new Sound("06", "#690eaa", 72 + offset));
  },

  methods: {
    onSoundSelected(index) {
      this.selectedSoundIndex = index;
    },
    onCellClicked(cell) {
      if (cell.sound == this.sounds[this.selectedSoundIndex]) cell.sound = null;
      else cell.sound = this.sounds[this.selectedSoundIndex];
    },
    onCellMouseDown(cell)
    {
      this.mouseDownTurningOff = cell.sound != null;
    },
    onCellMouseEntered(cell)
    {
      if (this.mouseDown)
      {
        if (this.mouseDownTurningOff) cell.sound = null;
        else cell.sound = this.sounds[this.selectedSoundIndex];
      }
    },
    onMouseDown() { this.mouseDown = true; },
    onMouseUp() { this.mouseDown = false; },
    onAudioButtonToggle() {
      this.audioOn = !this.audioOn;
      if (this.audioOn)
      {
        Sound.audioCtx.resume();
      }
      else
      {
        Sound.audioCtx.suspend();
      }
    },
  }
}
</script>




