export default class CellState
{
    constructor()
    {
        this.on = false;
        this.upcomingState = this.on;
        this.neighbours = [];
        this.sound = null;
        this.justPlayedSound = false;
    }

    getSoundColor()
    {
        return this.sound ? this.sound.color : "#fff0";
    }

    setDirect(newState)
    {
        this.on = newState;
        this.upcomingState = newState;
    }

    prepareNewState(newState)
    {
        this.upcomingState = newState;
    }
    applyNewState()
    {
        if (
            this.upcomingState == true && 
            this.on == false && 
            this.sound != null
            )
        {
            this.sound.play();
            this.justPlayedSound = true;
            var intervalId = setInterval(() => {
                this.justPlayedSound = false;
                clearInterval(intervalId);
            }, 170);
        }
        this.on = this.upcomingState;
    }
}