import Structure from "./Structure";

export default class Pulsar extends Structure
{
    constructor()
    {
        super();
        this.activeCells = [
            [0, 2],
            [0, 3],
            [0, 4],
            [0, 8],
            [0, 9],
            [0, 10],
            [5, 2],
            [5, 3],
            [5, 4],
            [5, 8],
            [5, 9],
            [5, 10],
            [7, 2],
            [7, 3],
            [7, 4],
            [7, 8],
            [7, 9],
            [7, 10],
            [12, 2],
            [12, 3],
            [12, 4],
            [12, 8],
            [12, 9],
            [12, 10],
            [2, 0],
            [3, 0],
            [4, 0],
            [8, 0],
            [9, 0],
            [10, 0],
            [2, 5],
            [3, 5],
            [4, 5],
            [8, 5],
            [9, 5],
            [10, 5],
            [2, 7],
            [3, 7],
            [4, 7],
            [8, 7],
            [9, 7],
            [10, 7],
            [2, 12],
            [3, 12],
            [4, 12],
            [8, 12],
            [9, 12],
            [10, 12],
        ];


        for (var x = 0; x < this.xs; x++)
        {
            for (var y = 0; y < this.ys; y++)
            {
                if ([0, 5, 7, 12].includes(x))
                {
                    if ([2, 3, 4, 8, 9, 10].includes(y))
                    {
                        this.activeCells.push([x, y]);
                    }
                }
                if ([0, 5, 7, 12].includes(y))
                {
                    if ([2, 3, 4, 8, 9, 10].includes(x))
                    {
                        this.activeCells.push([x, y]);
                    }
                }
            }
        }
    }
}