export default class Sound
{
    static audioCtx = new (window.AudioContext || window.webkitAudioContext);

    constructor(name, color, note)
    {
        this.name = name;
        this.color = color;

        this.wasTriggeredThisTick = false;

        this.master = Sound.audioCtx.createGain();
        this.master.gain.setValueAtTime(0.0, Sound.audioCtx.currentTime);

        this.maxGain = 1 / Math.sqrt(6);
        this.triggerAmps = [this.maxGain, this.maxGain * 0.78, this.maxGain * 0.55];
        this.trigger = 0;

        this.oscs = []
        this.gains = [];
        for (var i = 0; i < 5; i++)
        {
            var osc = Sound.audioCtx.createOscillator()
            var gain = Sound.audioCtx.createGain();
            this.oscs.push(osc);
            this.gains.push(gain);
            osc.start();
            osc.connect(gain);
            gain.connect(this.master);
        }

        this.setNote(note);
        
        this.master.connect(Sound.audioCtx.destination);
    }

    play()
    {
        if (this.wasTriggeredThisTick) return; // only play once per tick even if multiple cells are turned on
        let attack = 0.15 + Math.random() * 0.05;
        let release = 0.2 + Math.random() * 0.1;
        let now = Sound.audioCtx.currentTime;
        this.master.gain.cancelScheduledValues(now);
        this.master.gain.setTargetAtTime(this.triggerAmps[this.trigger % this.triggerAmps.length], now + attack, 0.001);
        this.master.gain.setTargetAtTime(0, now + attack + release, 0.3);

        this.wasTriggeredThisTick = true;
        this.trigger += 1;
    }

    midicps(note)
    {
        return (440.0 / 32.0) * (2.0 ** ((note - 9.0) / 12.0));
    }

    setNote(note)
    {
        let freq = this.midicps(note);
        let freqs = [freq, freq * 2, freq * 6, freq * 8, freq * 10];
        let amps =  [1.0,  0.05,     0.0105,     0.004,     0.004];
        for (var i = 0; i < freqs.length; i++)
        {
            this.oscs[i].frequency.setValueAtTime(freqs[i], Sound.audioCtx.currentTime)
            this.gains[i].gain.setValueAtTime(amps[i] * (1 / Math.sqrt(amps.length)), Sound.audioCtx.currentTime);
        }
    }
}